function attr_v(row, k) {
  switch (row[k]) {
    case '甲':
      return 0.3
    case '乙':
      return 0.2
    case '丙':
      return 0.1
    default:
      return 0
  }
}

function attr_value(row, word) {
  switch (word) {
    case row['属性1']:
      return attr_v(row, '属性1级别')
    case row['属性2']:
      return attr_v(row, '属性2级别')
    case row['属性3']:
      return attr_v(row, '属性3级别')
    case row['属性4']:
      return attr_v(row, '属性4级别')
    default:
      return 0
  }
}

function check(word1, word2, word3, row) {
  if (word1 == undefined || row == undefined)
    return [0, 0]

  const [w1, w2, w3] = [5, 2, 1]
  const [u1, u2] = [2, 1]

  let ret = 0

  if (word1 == row['主用途'] ||
    word1 == row['属性1'] ||
    word1 == row['属性2'] ||
    word1 == row['属性3'] ||
    word1 == row['属性4'] ||
    word1 == row['隐藏属性1'] ||
    word1 == row['隐藏属性2']) {
    ret += w1 * u1
    ret += attr_value(row, word1)
  } else if (word1 == row['次用途']) {
    ret += w1 * u2
  }

  if (word2 != undefined) {
    if (word2 == row['主用途'] ||
      word2 == row['属性1'] ||
      word2 == row['属性2'] ||
      word2 == row['属性3'] ||
      word2 == row['属性4'] ||
      word2 == row['隐藏属性1'] ||
      word2 == row['隐藏属性2']) {
      ret += w2 * u1
      ret += attr_value(row, word2)
    } else if (word2 == row['次用途']) {
      ret += w2 * u2
    }
  }

  if (word3 != undefined) {
    if (word3 == row['主用途'] ||
      word3 == row['属性1'] ||
      word3 == row['属性2'] ||
      word3 == row['属性3'] ||
      word3 == row['属性4'] ||
      word3 == row['隐藏属性1'] ||
      word3 == row['隐藏属性2']) {
      ret += w2 * u1
      ret += attr_value(row, word3)
    } else if (word2 == row['次用途']) {
      ret += w2 * u2
    }
  }

  if (word2 == undefined && word3 == undefined) {
    if (ret >= w1 * u1) {
      return [1, ret]
    } else if (ret > 0) {
      return [2, ret]
    } else {
      return [-1, ret]
    }
  } else if (word3 == undefined) {
    if (ret >= w1 * u1 + w2 * u1) {
      return [1, ret]
    } else if (ret > 0) {
      return [2, ret]
    } else {
      return [-1, ret]
    }
  } else {
    if (ret >= w1 * u1 + w2 * u1 + w3 * u1) {
      return [1, ret]
    } else if (ret > 0) {
      return [2, ret]
    } else {
      return [-1, ret]
    }
  }
}

function go(clothes, keywords) {
  let result = {}
  clothes.forEach((row) => {
    if (result[row['部位']] == undefined) {
      result[row['部位']] = { 1: [], 2: [] }
    }

    let [val, score] = check(keywords[0], keywords[1], keywords[2], row)
    if (val <= 0) {
      return
    }

    result[row['部位']][val].push({
      'name': row['名称'],
      'score': score,
      'ord': result[row['部位']][val].length
    })
  })

  let p = (x, y) => {
    if (y['score'] > x['score'])
      return 1
    else if (y['score'] < x['score'])
      return -1
    else
      return x['ord'] - y['ord']
  }
  for (let k in result) {
    result[k][1].sort(p)
    result[k][2].sort(p)
  }

  return result
}

function run(keywords) {
  if (keywords == '') {
    return []
  }

  let girl_result = go(clothes_girl, keywords)
  let boy_result = go(clothes_boy, keywords)

  let f = (label, result) => {
    return {
      label: label,
      results: [
        {
          label: '发饰',
          match1: result['发饰'][1].map((item) => { return item['name'] }).join(', '),
          match2: result['发饰'][2].map((item) => { return item['name'] }).join(', ')
        },
        {
          label: '服饰',
          match1: result['服饰'][1].map((item) => { return item['name'] }).join(', '),
          match2: result['服饰'][2].map((item) => { return item['name'] }).join(', ')
        },
        {
          label: '配饰',
          match1: result['配饰'][1].map((item) => { return item['name'] }).join(', '),
          match2: result['配饰'][2].map((item) => { return item['name'] }).join(', ')
        },
        {
          label: '鞋子',
          match1: result['鞋子'][1].map((item) => { return item['name'] }).join(', '),
          match2: result['鞋子'][2].map((item) => { return item['name'] }).join(', ')
        },
        {
          label: '手持',
          match1: result['手持'][1].map((item) => { return item['name'] }).join(', '),
          match2: result['手持'][2].map((item) => { return item['name'] }).join(', ')
        }
      ]
    }
  }

  return [
    f('女版', girl_result),
    f('男版', boy_result)
  ]
}
</script>


